import React from 'react';
import { FontAwesome } from '@expo/vector-icons';
import { themePattern } from '../../constants/pattern';
import { StyleSheet, Text, View, TouchableOpacity,Dimensions } from 'react-native';
import { PropsButtunsflats } from '../../types';

const { width, height } = Dimensions.get('window')


function BtnFlatBlackWhite(props: PropsButtunsflats) {

  // Função ancora
  let onPress = () => {
    props.onPress();
  };

  return (
    <TouchableOpacity
      onPress={() => onPress()}
      style={styles.btnCircle}
    >
      <View style={styles.iconContainer}>
        <View style={styles.icon}>
          <FontAwesome

            name={props.icon}
            style={
              styles.iconFont
            }

          />
        </View>
      </View>
    </TouchableOpacity>



  );
}



function Styles() {

  //Mobile
  if(themePattern.breakpoint.mobile >= width)
    return StyleSheet.create({
      btnCircle: {
        alignItems: "center",
      },
      icon: {
        width: "100%",
        height: "100%",
    
        flex: 1,
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
    
    
    
    
        alignItems: "center",
    
      },
      iconFont: {
        color: themePattern.mobile.black,
        fontSize: 24
      },
      iconContainer: {
        backgroundColor: themePattern.mobile.white,
        color: themePattern.mobile.black,
        width: 56,
        height: 56,
        borderRadius: 50,
        elevation: 4,
        shadowColor: 'black',
        shadowOffset: { width: 0, height: 4 },
        shadowOpacity: 0.25,
        shadowRadius: 4,
        
      }
    });
    
  
  //Tablet
  else
  return StyleSheet.create({
    btnCircle: {
      alignItems: "center",
    },
    icon: {
      width: "100%",
      height: "100%",
  
      flex: 1,
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
  
  
  
  
      alignItems: "center",
  
    },
    iconFont: {
      color: themePattern.mobile.black,
      fontSize: 36
    },
    iconContainer: {
      backgroundColor: themePattern.mobile.white,
      color: themePattern.mobile.black,
      width: 78,
      height: 78,
      borderRadius: 50,
      elevation: 4,
      shadowColor: 'black',
      shadowOffset: { width: 0, height: 4 },
      shadowOpacity: 0.25,
      shadowRadius: 4,
      
    }
  });
  
}

const styles =  Styles()

export default BtnFlatBlackWhite;