import React from 'react';
import { FontAwesome } from '@expo/vector-icons';
import { themePattern } from '../../constants/pattern';
import { StyleSheet, Text, View, TouchableOpacity, Dimensions } from 'react-native';
import { PropsButtuns } from '../../types';

const { width, height } = Dimensions.get('window')


function BtnCircle(props: PropsButtuns) {

  // Função ancora
  let onPress = () => {
    props.onPress();
  };

  return (
    <TouchableOpacity
      onPress={() => onPress()}
      style={styles.btnCircle}
    >
      <View style={styles.iconContainer}>
        <View style={styles.icon}>
          <FontAwesome

            name={props.icon}
            style={
              styles.iconFont
            }

          />
        </View>
      </View>
      <Text style={styles.title}>{props.name}</Text>
    </TouchableOpacity>



  );
}


function Styles() {

  //Mobile
  if(themePattern.breakpoint.mobile >= width)
    return StyleSheet.create({
      btnCircle: {
        alignItems: "center",
        width:100
      },
      icon: {
        width: "100%",
        height: "100%",
    
        flex: 1,
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
    
    
    
    
        alignItems: "center",
    
      },
      iconFont: {
        color: themePattern.mobile.white,
        fontSize: 24
      },
      iconContainer: {
        backgroundColor: themePattern.mobile.color1,
        color: themePattern.mobile.white,
        width: 56,
        height: 56,
        borderRadius: 50,
        elevation: 4,
        shadowColor: 'black',
        shadowOffset: { width: 0, height: 4 },
        shadowOpacity: 0.25,
        shadowRadius: 4,
        
      }
      , title: {
        marginTop: 5,
        fontSize: themePattern.mobile.par3,
        color: themePattern.mobile.gray
    
      }
    });
  
  //Tablet
  else
  return StyleSheet.create({
    btnCircle: {
      alignItems: "center",
      width:100
    },
    icon: {
      width: "100%",
      height: "100%",
  
      flex: 1,
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
  
  
  
  
      alignItems: "center",
  
    },
    iconFont: {
      color: themePattern.mobile.white,
      fontSize: 36
    },
    iconContainer: {
      backgroundColor: themePattern.mobile.color1,
      color: themePattern.mobile.white,
      width: 74,
      height: 74,
      borderRadius: 50,
      elevation: 4,
      shadowColor: 'black',
      shadowOffset: { width: 0, height: 4 },
      shadowOpacity: 0.25,
      shadowRadius: 4,
      
    }
    , title: {
      marginTop: 5,
      fontSize: themePattern.mobile.par3,
      color: themePattern.mobile.gray
  
    }
  });
}

const styles =  Styles()

export default BtnCircle;