

import * as React from 'react';
import { Button, View, Text, ScrollView, StyleSheet, Dimensions } from 'react-native';
import { themePattern } from '../constants/pattern';
import { PropsStackCredits } from './../types';

const { width, height } = Dimensions.get('window')


export default function Credits({ navigation }: PropsStackCredits) {
  return (
      <ScrollView style={styles.Main}>
        <Text style={styles.topico}>Coordenação Geral</Text>
        <Text style={styles.pessoa}>
          Leila M. Beltramini
        </Text>


        <Text style={styles.topico}>Consultor técnico</Text>
        <Text style={styles.pessoa}>Simone F. Souza{"\n"}Thiago Nicola Cajuela Garcia

</Text>


        <Text style={styles.topico}>Desenvolvimento do Software</Text>
        <Text style={styles.pessoa}>Claudinei C. de Souza (versão 1.0){"\n"}
          Rodrigo C. Diogo (versão 1.0){"\n"}
          Giovanni Wesley da Costa (versão 2.0){"\n"}
          William Luis Alves Ferreira (versão 2.0)</Text>

        <Text style={styles.topico}>Design de interface e conteúdo {"\n"} (versão 2.0)</Text>
        <Text style={styles.pessoa}>
          Gabriela de Carvalho Fatore{"\n"}
          Giovanni Wesley da Costa{"\n"}
          Simone F. Souza{"\n"}
          William Luis Alves Ferreira</Text>

        <Text style={styles.topico}>Supervisão</Text>
        <Text style={styles.pessoa}>
          Gislaine Costa{"\n"}

          Nelma R. S. Bossolan</Text>

          <Text style={styles.topico}>Software de Terceiros</Text>
        <Text style={styles.pessoa}>
        The MolView Project
        {"\n"}
        {"\n"}
        {"\n"}
        {"\n"}</Text>


      </ScrollView>

  );
}

function Styles() {

  //Mobile
  if(themePattern.breakpoint.mobile >= width)
    return StyleSheet.create({
      Main: {
        backgroundColor:themePattern.mobile.white,
        borderColor: themePattern.mobile.black,
        borderStyle: 'solid',
        borderWidth: 2,
        borderRadius: 15,
        padding: 15,
        paddingBottom: 30,
        margin: 15,
        textAlign: 'center',
        alignContent: 'center'
      },
      topico: {
        color: themePattern.mobile.color1,
        marginTop: 20,
        fontSize: themePattern.mobile.par1,
        fontWeight: 'bold',    
        textAlign: 'center',
        width:'100%'
    
      },
      pessoa: {
        color: themePattern.mobile.black,
        textAlign: 'center',
        marginTop: 5,
        fontSize: themePattern.mobile.par2
    
    
    
      }
    });
  
  //Tablet
  else
  return StyleSheet.create({
    Main: {
      backgroundColor:themePattern.tablet.white,
      borderColor: themePattern.tablet.black,
      borderStyle: 'solid',
      borderWidth: width <= 800 ? 2 : 0,
      borderRadius: 15,
      padding: 15,
      paddingBottom: 30,
      margin: 15,
      textAlign: 'center',
      alignContent: 'center'
    },
    topico: {
      color: themePattern.tablet.color1,
      marginTop: 20,
      fontSize: themePattern.tablet.title3,
      fontWeight: 'bold',    
      textAlign: 'center',
      width:'100%'
  
    },
    pessoa: {
      color: themePattern.tablet.black,
      textAlign: 'center',
      marginTop: 5,
      fontSize: themePattern.tablet.par1
  
  
  
    }
  });
}

const styles =  Styles()